<template>
  <div>
    <h3> . </h3>
    <div id='map'>
    </div>
    <v-row>
      <v-col cols="2"></v-col>
      <v-col cols="8">
        <p>
                .     
          tooltip        .   
                   .
               OSM    .   .<br>
           <a href="https://leafletjs.com/examples/choropleth/"> </a>.<br>
               ,       
                 geoJSON-, ..  
             ,     
            (     ).
           ,     ,  
          <a href="https://turfjs.org/docs/#booleanContains">Turf.js</a>
        </p>
      </v-col>
      <v-col cols="2"></v-col>
    </v-row>
  </div>
</template>

<script>
import L from 'leaflet'
import 'leaflet/dist/leaflet.css'
import 'leaflet.markercluster/dist/MarkerCluster.css'
import 'leaflet.markercluster/dist/MarkerCluster.Default.css'
import 'leaflet.markercluster/dist/leaflet.markercluster.js'
import booleanContains from '@turf/boolean-contains'
import FetchGeojson from '../scripts/fetch_geojson'

delete L.Icon.Default.prototype._getIconUrl
L.Icon.Default.mergeOptions({
  iconRetinaUrl: require('leaflet/dist/images/marker-icon-2x.png'),
  iconUrl: require('leaflet/dist/images/marker-icon.png'),
  shadowUrl: require('leaflet/dist/images/marker-shadow.png')
})

export default {
  name: 'Schools',
  data () {
    return {
      districts: null,
      schools: null,
      map: null,
      markers: null,
      maxSchools: 0,
      minSchools: 0
    }
  },
  methods: {
    initMap () {
      const izhevsk = [56.850671, 53.204795]
      /// const zero = [0.0, 0.0]
      const map = L.map('map')
        .setView(izhevsk, 11)
      L.tileLayer('https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png', {
        attribution: '&copy; <a href="https://www.openstreetmap.org/copyright">OSM</a> contributors'
      }).addTo(map)
      this.map = map
    },
    addTooltip (feature, layer) {
      if (feature.properties &&
        feature.properties.cntSchools &&
        feature.properties.name
      ) {
        const tooltip = feature.properties.name + ', ' + feature.properties.cntSchools.toString()
        layer.bindTooltip(tooltip)
      }
    },
    rgbToHex (r, g, b) {
      return '#' + ((1 << 24) + (r << 16) + (g << 8) + b).toString(16).slice(1)
    },
    getColorComponent (i, upperTreshold, lowerTreshold) {
      const offset = (i - this.minSchools) / (this.maxSchools - this.minSchools)
      const offset2 = 1 - offset
      const offsetInt = Math.round((upperTreshold - lowerTreshold) * offset2)
      return lowerTreshold + offsetInt
    },
    getColor (i) {
      const r = this.getColorComponent(i, 239, 117)
      const g = this.getColorComponent(i, 237, 107)
      const b = this.getColorComponent(i, 245, 177)
      return this.rgbToHex(r, g, b)
    },
    addDistricts () {
      const ref = this
      const districtsGroup = L.geoJSON(this.districts, {
        onEachFeature: ref.addTooltip,
        style: function (feature) {
          return {
            fillColor: ref.getColor(feature.properties.cntSchools),
            weight: 2,
            opacity: 1,
            color: 'white',
            dashArray: '3',
            fillOpacity: 0.7
          }
        }
      })
      districtsGroup.addTo(this.map)
    },
    addSchools () {
      const markers = L.markerClusterGroup()
      var geoJsonLayer = L.geoJson(this.schools, {
        onEachFeature: function (feature, layer) {
          layer.bindPopup(feature.properties.name)
        }
      })
      markers.addLayer(geoJsonLayer)
      this.map.addLayer(markers)
      this.markers = markers
    },
    countSchoolsInside () {
      const schoolsByDistrict = []
      for (const district of this.districts.features) {
        let total = 0
        for (const school of this.schools.features) {
          if (booleanContains(district, school)) {
            total += 1
          }
        }
        district.properties.cntSchools = total
        schoolsByDistrict.push(total)
      }
      this.maxSchools = schoolsByDistrict.reduce((prev, i) => i > prev ? i : prev)
      this.minSchools = schoolsByDistrict.reduce((prev, i) => i < prev ? i : prev)
    }
  },
  async mounted () {
    this.districts = await FetchGeojson('data/izhevsk_districts3.geojson')
    this.schools = await FetchGeojson('data/izhevsk_schools3.geojson')
    this.countSchoolsInside()
    this.initMap()
    this.addDistricts()
    this.addSchools()
  }
}
</script>

<style scoped>
h3 {
  text-align: center;
}

#map {
  width: 100%;
  height: 90vh;
}
</style>
